header {
 background-image: url(../images/th.png);
 text-shadow: 1px 1px 2px #000000;
}
#header-wrapper {
 display: flex;
 flex-direction: row;
 align-items: center;
 background: linear-gradient(45deg, #0798abfa, transparent);
}

header img {
 border: 4px solid #fff;
 background-color: #ffffff90;
 border-radius: 50%;
 margin: 5px;
}
